@import url("https://fonts.googleapis.com/css2?family=Epilogue:wght@500;700&display=swap");
* {
  padding: 0;
  margin: 0;
  font-family: "Epilogue";
}
*, *::before, *::after {
  box-sizing: border-box !important;
}

html {
  width: 100%;
  height: 100vh;
  overflow: hidden;
}

body {
  width: 100%;
  position: relative;
  height: 100vh;
  display: flex;
  padding: 0.1rem;
  flex-direction: column;
  background-color: hsl(0deg, 0%, 98%);
  overflow: hidden;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

.attribution {
  width: fit-content;
  font-size: 11px;
  text-align: center;
  position: absolute;
  left: 50%;
  bottom: 1rem;
  transform: translateX(-50%);
  z-index: 9;
}
@media (max-width: 700px) {
  .attribution {
    bottom: 0;
  }
}
.attribution a {
  color: hsl(228deg, 45%, 44%);
}

header {
  display: grid;
  grid-template-columns: max-content 1fr;
  padding: 0.75rem 0.75rem;
  align-items: center;
  flex: auto 0;
}
@media (max-width: 700px) {
  header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  header .sidebar-toggler {
    display: block;
  }
}
header a {
  color: hsl(0deg, 0%, 41%);
}
header a:hover {
  color: hsl(0deg, 0%, 8%);
}

.overlay {
  position: absolute;
  inset: 0;
  display: none;
  background-color: rgba(0, 0, 0, 0.2);
}

@media (max-width: 700px) {
  .links-container.opened ~ .overlay {
    display: block;
  }
}

.links-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 5%;
}
.links-container .close-btn {
  display: none;
}
@media (max-width: 700px) {
  .links-container {
    background-color: white;
    display: inline-block;
    position: absolute;
    inset: 0 0 0 auto;
    transition: transform 0.5s ease;
    transform: translateX(100%);
    min-width: 50%;
    z-index: 9999999;
    width: 70%;
  }
  .links-container .close-btn {
    display: inline-block;
    width: 100%;
    text-align: right;
    padding-top: 0.5rem;
  }
  .links-container.closed {
    display: none;
  }
  .links-container.openinig {
    transform: translateX(100%);
  }
  .links-container.closing {
    transform: translateX(100%);
  }
  .links-container.opened {
    transform: translateX(0%);
  }
}

.sidebar-toggler {
  display: none;
}

@keyframes slide-in {
  to {
    transform: translateX(0%);
  }
}
.nav-links {
  display: flex;
  gap: 0.8rem;
}
@media (max-width: 700px) {
  .nav-links {
    flex-direction: column;
    margin-top: 1rem;
    margin-bottom: 1rem;
    gap: 2rem;
  }
}

.nav-link.collapse .dropdown-content {
  display: none;
}
@media (min-width: 700px) {
  .nav-link.collapse:nth-of-type(1) .dropdown-content, .nav-link.collapsed:nth-of-type(1) .dropdown-content {
    transform: translateX(-40%);
  }
  .nav-link.collapse:nth-of-type(2) .dropdown-content, .nav-link.collapsed:nth-of-type(2) .dropdown-content {
    transform: translateX(0%);
  }
}
@media (max-width: 700px) {
  .nav-link.collapse .dropdown-content {
    display: none;
  }
  .nav-link.collapsed .dropdown-content {
    display: flex;
  }
}
.nav-link.collapsed > a::after, .nav-link.collapse > a::after {
  content: "";
  padding: 0px 0.75rem;
  background-position: center;
  background-repeat: no-repeat;
  filter: hue-rotate(0deg) saturate(0%) brightness(41%);
}
.nav-link.collapsed:hover a::after, .nav-link.collapse:hover a::after {
  filter: hue-rotate(0deg) saturate(0%) brightness(8%);
  mix-blend-mode: darken;
}
.nav-link[aria-expanded=true] > a::after {
  background-image: url(./images/icon-arrow-up.svg);
}
.nav-link[aria-expanded=false] > a::after {
  background-image: url(./images/icon-arrow-down.svg);
}

.nav-link .dropdown-content {
  background-color: white;
  margin-top: 1rem;
  padding: 1.25rem 0.25rem;
  border: none;
  display: flex;
  flex-direction: column;
  transform: translateX(0%);
  gap: 1.5rem;
}
@media (min-width: 700px) {
  .nav-link .dropdown-content {
    position: absolute;
    border-radius: 1rem;
    gap: 1rem;
    box-shadow: 0px 0px 1.5rem hsl(225deg, 20%, 80%);
  }
}
.nav-link .dropdown-content li {
  display: flex;
  align-items: center;
  padding: 0 1rem;
  gap: 1rem;
}
@media (max-width: 700px) {
  .nav-link .dropdown-content li {
    justify-content: flex-start;
  }
}

.header-right {
  display: flex;
  gap: 1.5rem;
  align-items: baseline;
}
.header-right .signup-btn {
  border: 2px solid hsl(0deg, 0%, 41%);
  padding: 0.75rem 1rem;
  border-radius: 0.5rem;
  transition: border-color 0.3s ease;
  text-align: center;
}
.header-right .signup-btn:hover {
  border-color: hsl(0deg, 0%, 8%);
}
@media (max-width: 700px) {
  .header-right .signup-btn {
    width: 100%;
  }
}
@media (max-width: 700px) {
  .header-right {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}

.main-content {
  flex: auto 1;
  display: grid;
  height: 100%;
}
@media (max-width: 700px) {
  .main-content {
    grid-template-rows: max-content 1fr;
    grid-template-areas: "two" "one";
  }
  .main-content .one {
    grid-area: one;
  }
  .main-content .two {
    grid-area: two;
  }
}
@media (min-width: 700px) {
  .main-content {
    padding: 3% 10vw 10% 10vw;
    grid-template-columns: repeat(2, 1fr);
  }
}

.content {
  display: flex;
  justify-content: center;
  align-items: center;
  align-items: flex-start;
  flex-direction: column;
  gap: 4rem;
}
@media (max-width: 700px) {
  .content {
    font-size: 12px;
    align-items: center;
    text-align: center;
  }
}

.site-description {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: inherit;
  align-items: flex-start;
  flex-direction: column;
  gap: 2.5em;
  padding-top: 0.5rem;
  max-width: 450px;
}
.site-description h2 {
  font-size: 4.25em;
  font-weight: 700;
}
@media (max-width: 700px) {
  .site-description h2 {
    font-size: 3.5em;
  }
}
.site-description p {
  font-size: 18px;
  line-height: 1.5;
  color: hsl(0deg, 0%, 41%);
  width: 45ch;
}
@media (max-width: 700px) {
  .site-description p {
    width: 90vw;
  }
}
@media (max-width: 700px) {
  .site-description {
    justify-content: flex-start;
    align-items: center;
    gap: 2em;
  }
}

.icons {
  font-size: inherit;
  display: flex;
  gap: 1.75em;
  align-items: center;
}
@media (max-width: 700px) {
  .icons {
    gap: 1em;
  }
  .icons img {
    width: 4em;
  }
}

.learn-more-btn {
  display: block;
  background-color: hsl(0deg, 0%, 8%);
  color: hsl(0deg, 0%, 98%);
  padding: 0.8rem 1.6rem;
  border-radius: 0.5rem/0.5rem;
}

.bg-img {
  background-image: url(./images/image-hero-desktop.png);
  background-repeat: no-repeat;
  background-size: contain !important;
  background-position: center;
  min-width: 36vw;
}
@media (max-width: 700px) {
  .bg-img {
    background-size: cover !important;
    height: calc(28vh + 20vw);
    background-image: url(./images/image-hero-mobile.png);
  }
}

.row-span-2 {
  grid-template-rows: span 2;
}

