*{
    padding: 0;
    margin: 0;
    &, &::before, &::after{
        box-sizing: border-box !important;
    }
    font-family: $font;
}

html{
    width: 100%;
    height: 100vh;
    overflow: hidden;
}

body{
    width: 100%;
    position: relative;
    height: 100vh;
    display: flex;
    padding: 0.1rem;
    flex-direction: column;
    background-color: $almost-white;
    overflow: hidden;
}

a{
    text-decoration: none;
}

ul{
    list-style: none;
}

.attribution{
    width: fit-content;
    font-size: 11px; 
    text-align: center;
    position: absolute; 
    left: 50%;
    bottom: 1rem;
    transform: translateX(-50%);
    z-index: 9;

    @include mobile{
        bottom: 0;
    }

    a{
        color: hsl(228, 45%, 44%);
    }
}

