header{
    display: grid;
    grid-template-columns: max-content 1fr;
    padding: 0.75rem 0.75rem;
    align-items: center;
    flex: auto 0;

    @include mobile{
        @include content-between;
        align-items: center;
        
        .sidebar-toggler{
            display: block;
        }
    }

    a{
        color: $medium-gray;
    }
    
    a:hover{
        color: $almost-black;
    }
}

.overlay{
    position: absolute;
    inset: 0;
    display: none;
    background-color: rgb(0 0 0 / 0.2);   
}

.links-container.opened ~ .overlay{
    @include mobile{
        display: block;
    }
}

.links-container{
    @include content-between;
    align-items: center;
    padding: 0 5%;

    .close-btn{
        display: none;
    }

    @include mobile{
        background-color: white;
        display: inline-block;
        position: absolute;
        inset: 0 0 0 auto;
        transition: transform .5s ease;
        transform: translateX(100%);
        min-width: 50%;
        z-index: 9999999;
        width: 70%;

        .close-btn{
            display: inline-block;
            width: 100%;
            text-align: right;
            padding-top: 0.5rem;
        }

        &.closed{
            display: none;
        }

        &.openinig{
            transform: translateX(100%);
        }

        &.closing{
            transform: translateX(100%);
        }

        &.opened{
            transform: translateX(0%);
        }
    }
}

.sidebar-toggler{
    display: none;
}

@keyframes slide-in {
    to{
        transform: translateX(0%);
    }
}

.navbar{
    
}

.nav-links{
    display: flex;
    gap: 0.8rem;
    @include mobile{
        flex-direction: column;
        margin-top: 1rem;
        margin-bottom: 1rem;
        gap: 2rem;
    }
}

.nav-link{
    &.collapse{
        .dropdown-content{
            display: none;
        }
    }
    @include big{
        &.collapse:nth-of-type(1), &.collapsed:nth-of-type(1){
            .dropdown-content{
                transform: translateX(-40%);
            }
        }
        &.collapse:nth-of-type(2), &.collapsed:nth-of-type(2){
            .dropdown-content{
                transform: translateX(0%);
            }
        }
    }

    @include mobile{
        &.collapse{
            .dropdown-content{
                display: none;
            }
        }

        &.collapsed{
            .dropdown-content{
                display: flex;
            }
        }
    }

    &.collapsed > a::after, &.collapse > a::after{
        content: '';
        padding: 0px 0.75rem;
        background-position: center;
        background-repeat: no-repeat;
        filter: hue-rotate(0deg) saturate(0%) brightness(41%);
    }

    &.collapsed:hover a::after, &.collapse:hover a::after{
        filter: hue-rotate(0deg) saturate(0%) brightness(8%);
        mix-blend-mode: darken;
    }

    &[aria-expanded=true] > a::after{
        background-image: url(./images/icon-arrow-up.svg);
    }
    &[aria-expanded=false] > a::after{
        background-image: url(./images/icon-arrow-down.svg);
    }
}

.nav-link .dropdown-content{  
    background-color: white;
    margin-top: 1rem;
    padding: 1.25rem 0.25rem;
    border: none;
    display: flex;
    flex-direction: column;
    transform: translateX(0%);
    gap: 1.5rem;

    @include big{
        position: absolute;
        border-radius: 1rem;
        gap: 1rem;
        box-shadow: 0px 0px 1.5rem hsl(225, 20%, 80%);
    }

    li{
        display: flex;
        align-items: center;
        padding: 0 1rem;
        gap: 1rem;

        @include mobile{
            justify-content: flex-start;
        }
    }
}

.header-right{
    display: flex;
    gap: 1.5rem;
    align-items: baseline;

    .signup-btn{
        border: 2px solid $medium-gray;
        padding: 0.75rem 1rem;
        border-radius: .5rem;
        transition: border-color .3s ease;
        text-align: center;

        &:hover{
            border-color: $almost-black;
        }

        @include mobile{
            width: 100%;
        }
    }

    @include mobile{
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
}