.main-content{
    flex: auto 1;
    display: grid;
    height: 100%;

    @include mobile{
        .one{
            grid-area: one;
        }
        .two{
            grid-area: two;
        }

        grid-template-rows: max-content 1fr;

        grid-template-areas: 
            'two'
            'one'
        ;
    }

    @include big{
        padding: 3% 10vw 10% 10vw;
        grid-template-columns: repeat(2, 1fr);
    }
}

.content{
    @include content-center;
    align-items: flex-start;
    flex-direction: column;
    gap: 4rem;

    @include mobile{
        font-size: 12px;
        align-items: center;
        text-align: center
    }
}

.site-description{
    @include content-center;
    font-size: inherit;
    align-items: flex-start;
    flex-direction: column;
    gap: 2.5em;
    padding-top: 0.5rem;
    max-width: 450px;

    h2{
        font-size: 4.25em;
        font-weight: 700;

        @include mobile{
            font-size: 3.5em;
        }
    }

    p{
        font-size: $p-font-size;
        line-height: 1.5;
        color: $medium-gray;
        width: 45ch;

        @include mobile{
            width: 90vw;
        }
    }

    @include mobile{
        justify-content: flex-start;
        align-items: center;
        gap: 2em;
    }
}

.icons{
    font-size: inherit;
    display: flex;
    gap: 1.75em;
    align-items: center;

    @include mobile{
        gap: 1em;
        img{
            width: 4em;
        }
    }
}

.learn-more-btn{
    display: block;
    background-color: $almost-black;
    color: $almost-white;
    padding: 0.8rem 1.6rem;
    border-radius: .5rem / .5rem;
}

.bg-img{
    background-image: url(./images/image-hero-desktop.png);
    background-repeat: no-repeat;
    background-size: contain !important;
    background-position: center;
    min-width: 36vw;

    @include mobile{
        background-size: cover !important;
        height: calc(((70/100) * 40vh) + ((50/100) * 40vw));
        background-image: url(./images/image-hero-mobile.png);
    }
}

.row-span-2{
    grid-template-rows: span 2;
}