@mixin mobile {
    @media (max-width: 700px){
        @content;
    }
}

@mixin big {
    @media (min-width: 700px){
        @content;
    }
}

@mixin content-between {
    display: flex;
    justify-content: space-between;
}

@mixin content-center {
    display: flex;
    justify-content: center;
    align-items: center;
}